/**
 * External dependencies.
 */

/**
 * WordPress dependencies.
 */
import React from 'react';
import {__, sprintf} from '@wordpress/i18n';
import {render, useState} from '@wordpress/element';
import {subscribe, useDispatch} from '@wordpress/data';
import {Button, Modal} from '@wordpress/components';
import {BlockPreview} from '@wordpress/block-editor';
import usePatternsState from "./pattern-hook";
import useInsertionPoint from './insert-hook';
// include editor styles
import './editor.scss';
import AccordionPatternIcon from '../components/icons/accordion-pattern';


/**
 * Render the header toolbar button and the accompanying pattern explorer modal.
 *
 * @since 0.1.0
 * @return {string} Return the rendered JSX for the Pattern Explorer Button
 */
function HeaderToolbarButton() {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [allPatterns] = usePatternsState();
    const aab_premium = aagb_local_object.licensing;


    const {createSuccessNotice} = useDispatch('core/notices');
    const [destinationRootClientId, onInsertBlocks] = useInsertionPoint({ // eslint-disable-line
        shouldFocusBlock: true,
    });
    const handleAddPattern = (pattern) => {

        function cloneBlockWithNestedInnerBlocks(blockDef) {
            const innerBlocks = blockDef.innerBlocks ?
                blockDef.innerBlocks.map(cloneBlockWithNestedInnerBlocks) :
                [];

            return wp.blocks.cloneBlock(blockDef, {}, innerBlocks);
        }

        const blocksToInsert = pattern.blocks.map(cloneBlockWithNestedInnerBlocks);

        onInsertBlocks(blocksToInsert);


        setIsModalOpen(false);

        createSuccessNotice(
            sprintf(
                // Translators: Name of the pattern being inserted.
                __('Block pattern "%s" inserted.', 'advanced-accordion-block'),
                pattern.title
            ),
            {type: 'snackbar'}
        );
    };


    // Dynamically require all images from the `pro-patterns` folder
    const proPatternsContext = require.context('../../assets/img/pro-patterns', false, /\.(png|jpg|jpeg|gif|svg)$/);

    const proPatterns = proPatternsContext.keys().map((fileName) => ({
        name: fileName.replace('./', ''), // Extract the file name without './'
        src: proPatternsContext(fileName), // Get the image URL
    }));


    const sanitizeFileName = (fileName) => {
        return fileName
            .replace(/\.(png|jpg|jpeg|svg)$/i, '') // Remove the file extension (case-insensitive)
            .replace(/-/g, ' ');                  // Replace dashes with spaces
    };

    return (
        <>
            <Button
                className='has-icon'
                label={__('Explore Advanced Accordion Block Patterns', 'advanced-accordion-block')}
                onClick={() => setIsModalOpen(true)}
            >
                <AccordionPatternIcon />
            </Button>
            {isModalOpen && (
                <Modal
                    title={__('Advanced Accordion Block Patterns', 'advanced-accordion-block')}
                    closeLabel={__('Close', 'advanced-accordion-block')}
                    onRequestClose={() => setIsModalOpen(false)}
                    className="accordion_block_pattern__modal"
                    isFullScreen
                >
                    <div className='accordion__pattern_list_wrapper'>

                        {allPatterns.map((pattern) => {

                            const isPremium = !aab_premium && pattern.categories.includes('premium-advanced-accordion-block');

                            if(isPremium){
                                return ;
                            }
                            return (
                                <div
                                    className='accordion__pattern_list_item'
                                    key={pattern.name}
                                >
                                    <div className="tooltip-container">
                                        <button
                                            className="accordion__item_preview"
                                            onClick={() => handleAddPattern(pattern)}
                                        >
                                            <BlockPreview
                                                blocks={pattern.blocks}
                                            />
                                        </button>
                                    </div>
                                    <div className="accordion_list_item_footer">
                                        <div>{pattern.title}</div>
                                        <Button
                                            variant="primary"
                                            onClick={() => handleAddPattern(pattern)}
                                        >
                                            {__('Add Pattern', 'advanced-accordion-block')}
                                        </Button>
                                    </div>
                                </div>
                            );
                        })}

                        {!aab_premium && (
                            <>
                                {proPatterns.map((pattern, index) => (
                                    <div className="accordion__pattern_list_item" key={index}>
                                        <div className="tooltip-container">
                                            <div className="accordion__item_preview">
                                                <img src={pattern.src} alt={pattern.name}
                                                     className="pro-pattern-image"/>
                                            </div>
                                        </div>
                                        <div className="accordion_list_item_footer">
                                            <div>{sanitizeFileName(pattern.name)}</div>
                                            <span className="pro-badge"> Pro</span>
                                        </div>
                                    </div>
                                ))}
                            </>
                        )}
                    </div>
                </Modal>
            )}
        </>
    );
}

/**
 * Add the header toolbar button to the block editor.
 */
subscribe(() => {
    const inserter = document.querySelector('#accordion_block_patterns_inserter');

    // If the inserter already exists, bail.
    if (inserter) {
        return;
    }

    wp.domReady(() => {
        var toolbar = document.querySelector(
            '.edit-post-header-toolbar__left'
        );

        // If no toolbar can be found at all, bail.
        if (!toolbar) {
            toolbar = document.querySelector(
                '.editor-document-tools__left'
            );
        }

        // If no toolbar can be found at all, bail.
        if (!toolbar) {
            return;
        }

        const buttonContainer = document.createElement('div');
        buttonContainer.id = 'accordion_block_patterns_inserter';

        toolbar.appendChild(buttonContainer);

        render(
            <HeaderToolbarButton/>,
            document.getElementById('accordion_block_patterns_inserter')
        );
    });
});


