import { useSelect } from '@wordpress/data';
import { store as blockEditorStore } from '@wordpress/block-editor';
import { useMemo } from '@wordpress/element';

const usePatternsState = (rootClientId) => {
    const patterns = useSelect(
        (select) => {
            try {
                const { __experimentalGetAllowedPatterns, getSettings } = select(blockEditorStore);

                // Safely handle potential undefined or null return
                const allPatterns = __experimentalGetAllowedPatterns?.(rootClientId) || [];

                // Use memoization to prevent unnecessary re-renders
                return allPatterns.filter((pattern) =>
                    pattern.categories?.includes("advanced-accordion-block-category")
                );
            } catch (error) {
                console.error('Error fetching patterns:', error);
                return [];
            }
        },
        [rootClientId]
    );

    // Use useMemo to further optimize rendering
    return useMemo(() => [patterns], [patterns]);
};

export default usePatternsState;